}
@media (max-width: 1023px) {
  .service-hero {
    padding: 30px 0 50px;
  }
}
@media (max-width: 767px) {
  .service-hero {
    padding: 20px 0 24px;
    position: relative;
  }
  .service-hero::after {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    top: -64px;
    left: 0;
    right: 0;
    background: linear-gradient(180deg, rgba(255, 236, 218, 0.64) 1.83%, rgba(188, 153, 117, 0.2) 65.37%, rgba(97, 76, 57, 0) 89.62%);
  }
}

.service-hero__wrapper {
  position: relative;
  width: 100%;
  overflow: hidden;
}
.service-hero__wrapper::after {
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(to left, rgba(164, 133, 86, 0.1) 34%, rgba(251, 246, 238, 0.5) 47%, rgba(253, 253, 253, 0.9) 80%, #FDFDFD 100%);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
}
@media (max-width: 767px) {
  .service-hero__wrapper::after {
    display: none;
  }
}
.service-hero__wrapper .container {
  padding-top: 20%;
  padding-bottom: 8.2%;
}
@media (max-width: 767px) {
  .service-hero__wrapper .container {
    padding-top: 10%;
    padding-bottom: 6%;
  }
}

.service-hero__inner {
  padding: 0 40px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 60%;
  position: relative;
}
@media (max-width: 767px) {
  .service-hero__inner {
    padding: 0;
    width: 100%;
    gap: 20px;
  }
}
.service-hero__inner svg {
  position: absolute;
  bottom: -20px;
  left: 34px;
  opacity: 60%;
  z-index: -1;
  width: 20%;
  height: auto;
}
@media (max-width: 767px) {
  .service-hero__inner svg {
    width: 30px;
    left: -18px;
  }
}

.service-hero__image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
}
.service-hero__image img {
  width: 100%;
  height: 100%;
  object-position: top;
  object-fit: cover;
}
@media (max-width: 767px) {
  .service-hero__image {
    display: none;
  }
}

.service-hero__text {
  margin: 0;
  padding: 0;
  font-size: 20px;
  line-height: 140%;
}
@media (max-width: 1023px) {
  .service-hero__text {
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .service-hero__text {
    font-size: 12px;
  }
}

.service-main {
  display: flex;
  flex-direction: column;
  padding-bottom: 100px;
  font-size: 18px;
  line-height: 150%;
}
.service-main p,
.service-main ul,
.service-main li {
  text-align: justify;
  margin: 0;
}
@media (max-width: 1023px) {
  .service-main {
    padding-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .service-main {
    font-size: 14px;
  }
}

.service-main__top {
  display: inline;
}
.service-main__top p {
  display: inline;
  margin: 0;
}

.service-main__title {
  font-size: 20px;
  font-weight: 600;
}
@media (max-width: 767px) {
  .service-main__title {
    font-size: 16px;
  }
}

.service-main__middle {
  margin-top: 30px;
}
@media (max-width: 767px) {
  .service-main__middle {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding-bottom: 30px;
  }
}

.service-main__modal-link {
  display: flex;
  width: 435px;
  float: right;
  margin: 10px 0 10px 30px;
  padding-top: 0;
}
@media (max-width: 1023px) {
  .service-main__modal-link {
    width: 365px;
  }
  .service-main__modal-link > .modal-link__icon {
    padding: 15px;
  }
}
@media (max-width: 767px) {
  .service-main__modal-link {
    float: none;
    order: 2;
    width: 100%;
    margin: 0 auto;
  }
}
.service-main__modal-link .modal-link__button::after {
  width: 32%;
}
@media (max-width: 1023px) {
  .service-main__modal-link .modal-link__button::after {
    width: 22%;
  }
}
@media (max-width: 767px) {
  .service-main__modal-link .modal-link__button::after {
    width: 60px;
  }
}
.service-main__modal-link .modal-link__text {
  display: none;
}
.service-main__modal-link .modal-link__short-text {
  display: flex;
}
.service-main__modal-link .modal-link__wrapper {
  display: flex;
}

.service-main__bottom {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: flex-start;
  padding: 30px 0;
}
@media (max-width: 767px) {
  .service-main__bottom {
    padding: 20px 0;
    flex-direction: column;
    gap: 15px;
  }
}

.service-main__link {
  width: 40%;
  display: flex;
  align-items: flex-end;
  transition: color 0.3s ease;
  font-size: 18px;
  padding: 0;
}
@media (max-width: 767px) {
  .service-main__link {
    width: 100%;
    font-size: 14px;
  }
}
@media (hover: hover) {
  .service-main__link:hover:not(.focus-visible) {
    color: rgba(239, 152, 64, 0.8);
  }
}
.service-main__link.focus-visible:focus {
  color: rgba(239, 152, 64, 0.8);
}

